import React, { useState, useEffect } from 'react';
import axios from 'axios';
import PropTypes from 'prop-types';
import { platform, IOS, Input } from '@vkontakte/vkui';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import PanelHeaderButton from '@vkontakte/vkui/dist/components/PanelHeaderButton/PanelHeaderButton';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';

const osName = platform();




function Hero() {
	const [heroes, setHeroes] = useState([]);


	function getUser() {
		//setHeroes(<ScreenSpinner size='large' />);
		axios.get('https://skynets.space/hero')
			.then(function (response) {
				// handle success	
				console.log(response.data);
				setHeroes(response.data.items);

			})
			.catch(function (error) {
				// handle error
				console.log(error);
			})
			.finally(function () {
				// always executed
			});
	}

	return (
		<div>
			<Input
				type="text"
				name="FIO"
				placeholder="ФИО"
			/>
			<Div>
				<Button size="xl" level="2" onClick={getUser}>
					Поиск героев
			</Button>
			</Div>
			<ul>
				{heroes.map(item => (
					<li key={item.id}>{item.Lastname} {item.Firstname} {item.Patronymic}</li>
				))}
			</ul>
		</div>
	);
}

const Album = props => (

	<Panel id={props.id}>
		<PanelHeader
			left={<PanelHeaderButton onClick={props.go} data-to="home">
				{osName === IOS ? <Icon28ChevronBack /> : <Icon24Back />}
			</PanelHeaderButton>}
		>
			Галлерея героев
		</PanelHeader>

		< Hero />

	</Panel>

);

Album.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Album;
